'use client'

import { useEffect, useState } from 'react'
import { api } from '@/lib/axios'
import { Input } from '@/components/ui/input'
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table'
import { Button } from '@/components/ui/button'
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from '@/components/ui/dialog'

interface User {
  id: string
  name: string
  email: string
  type: string
}

export default function EditUsers() {
  const [users, setUsers] = useState<User[]>([])
  const [selectedUser, setSelectedUser] = useState<User | null>(null)
  const [editedName, setEditedName] = useState('')
  const [editedEmail, setEditedEmail] = useState('')
  const [openDialog, setOpenDialog] = useState(false)

  // Buscar usuários ao carregar a página
  useEffect(() => {
    fetchUsers()
  }, [])

  async function fetchUsers() {
    try {
      const response = await api.get('/users')
      setUsers(response.data.users)
    } catch (error) {
      console.error('Erro ao buscar usuários:', error)
    }
  }

  // Abrir modal de edição
  function handleEditUser(user: User) {
    setSelectedUser(user)
    setEditedName(user.name)
    setEditedEmail(user.email)
    setOpenDialog(true)
  }

  // Salvar alterações do usuário
  async function handleSaveChanges() {
    if (!selectedUser) return

    try {
      await api.put(`/users/edit/${selectedUser.id}`, {
        name: editedName,
        email: editedEmail,
      })

      setOpenDialog(false)
      fetchUsers()
    } catch (error) {
      console.error('Erro ao atualizar usuário:', error)
    }
  }

  // Excluir usuário
  async function handleDeleteUser(userId: string) {
    const confirmDelete = window.confirm(
      'Tem certeza que deseja excluir este usuário?',
    )

    if (confirmDelete) {
      try {
        await api.delete(`/users/delete/${userId}`)
        fetchUsers()
      } catch (error) {
        console.error('Erro ao excluir usuário:', error)
      }
    }
  }

  return (
    <div className="p-6 max-w-5xl mx-auto bg-gray-900 text-white rounded-lg shadow-lg">
      <h1 className="text-2xl font-bold mb-4 text-center">
        Gerenciar Usuários
      </h1>

      <div className="overflow-x-auto">
        <Table className="w-full border border-gray-700">
          <TableHeader className="bg-gray-800">
            <TableRow>
              <TableHead className="text-left text-gray-300">Nome</TableHead>
              <TableHead className="text-left text-gray-300">Email</TableHead>
              <TableHead className="text-left text-gray-300">Ações</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {users.map((user) => (
              <TableRow key={user.id} className="border-b border-gray-700">
                <TableCell>{user.name}</TableCell>
                <TableCell>{user.email}</TableCell>
                <TableCell>
                  <div className="flex gap-2">
                    {/* Abrir modal corretamente */}
                    <Dialog>
                      <DialogTrigger asChild>
                        <Button
                          variant="outline"
                          onClick={() => handleEditUser(user)}
                        >
                          Editar
                        </Button>
                      </DialogTrigger>
                      <DialogContent className="bg-gray-800 text-white">
                        <DialogHeader>
                          <DialogTitle>Editar Usuário</DialogTitle>
                        </DialogHeader>
                        <div className="space-y-4">
                          <Input
                            placeholder="Nome"
                            value={editedName}
                            onChange={(e) => setEditedName(e.target.value)}
                            className="bg-gray-700 text-white"
                          />
                          <Input
                            placeholder="Email"
                            value={editedEmail}
                            onChange={(e) => setEditedEmail(e.target.value)}
                            className="bg-gray-700 text-white"
                          />
                        </div>
                        <div className="flex justify-end gap-2">
                          <Button variant="outline">Cancelar</Button>
                          <Button onClick={handleSaveChanges}>Salvar</Button>
                        </div>
                      </DialogContent>
                    </Dialog>

                    <Button
                      variant="destructive"
                      onClick={() => handleDeleteUser(user.id)}
                    >
                      Excluir
                    </Button>
                  </div>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>

      {/* Modal de edição */}
      <Dialog open={openDialog} onOpenChange={setOpenDialog}>
        <DialogContent className="bg-gray-800 text-white">
          <DialogHeader>
            <DialogTitle>Editar Usuário</DialogTitle>
          </DialogHeader>
          <div className="space-y-4">
            <Input
              placeholder="Nome"
              value={editedName}
              onChange={(e) => setEditedName(e.target.value)}
              className="bg-gray-700 text-white"
            />
            <Input
              placeholder="Email"
              value={editedEmail}
              onChange={(e) => setEditedEmail(e.target.value)}
              className="bg-gray-700 text-white"
            />
          </div>
          <div className="flex justify-end gap-2">
            <Button variant="outline" onClick={() => setOpenDialog(false)}>
              Cancelar
            </Button>
            <Button onClick={handleSaveChanges}>Salvar</Button>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  )
}
